<template>
  <body>
    <div id="backgroundTriangle"></div>
    <HeaderLogo />
    <Notification />
    <header id="headerLeft">
      <Navigation />
      <UserCard />
    </header>
    <main id="mainTasksSection">
      <div id="mainTasksContainer">
        <h1 class="titles">Do zrobienia</h1>
        <section id="tasksSection">
          <div id="tasksHeader">
            <div
              @click="generalStore.activeTaskElement = 'home'"
              class="tasksHeaderElement"
            >
              <router-link to="/dashboard/zadania">
                <svg-icon
                  type="mdi"
                  :path="iconsStore.mdiHomeOutline"
                ></svg-icon>
                <span>Statystyki</span>
              </router-link>
            </div>
            <div
              @click="generalStore.activeTaskElement = 'addTask'"
              class="tasksHeaderElement"
            >
              <router-link to="/dashboard/zadania">
                <svg-icon
                  type="mdi"
                  :path="iconsStore.mdiPlusCircleOutline"
                ></svg-icon>
                <span>Dodaj zadanie</span>
              </router-link>
            </div>
          </div>
          <div id="tasksList">
            <ul id="tasksListScroll">
              <div id="tasksListHeader">
                <span>To Do</span>
                <hr />
              </div>
              <div v-if="tasks.length === 0" class="emptyValuesMessage">
                <h1>Brak zadań do wyświetlenia</h1>
              </div>
              <li
                v-for="task in tasks"
                :key="task.id"
                class="taskListElement pointer"
              >
                <div @click="changeTask(task._id)">
                  <svg-icon
                    v-if="task.status === 'cancelled'"
                    class="taskListElementStatus cancelled"
                    type="mdi"
                    :path="iconsStore.mdiCloseCircleOutline"
                  ></svg-icon>
                  <svg-icon
                    v-else-if="task.status === 'finished'"
                    class="taskListElementStatus finished"
                    type="mdi"
                    :path="iconsStore.successIcon"
                  ></svg-icon>
                  <svg-icon
                    v-else-if="task.status === 'inProgress'"
                    class="taskListElementStatus inProgress"
                    type="mdi"
                    :path="iconsStore.mdiDotsHorizontalCircleOutline"
                  ></svg-icon>
                  <svg-icon
                    v-else
                    class="taskListElementStatus available"
                    type="mdi"
                    :path="iconsStore.mdiRecordCircleOutline"
                  ></svg-icon>
                  <div class="taskListElementDetails">
                    <span class="taskListElementID">
                      {{ `#${task._id}` }}
                    </span>
                    <h1 class="taskListElementTitle">
                      {{ task.title }}
                    </h1>
                  </div>
                  <svg-icon
                    v-if="task.important"
                    class="taskListElementStar"
                    type="mdi"
                    :path="iconsStore.mdiStar"
                  ></svg-icon>
                </div>
              </li>
            </ul>
          </div>
          <section id="taskDetailsSection">
            <div id="taskDetails">
              <div id="taskDetailsEditMessage" :style="styles.editMsg">
                Tryb edycji włączony
              </div>
              <div id="taskDetailsHeader">
                <svg-icon
                  v-if="newTask.status === 'cancelled'"
                  class="taskDetailsHeaderStatusIcon cancelled"
                  type="mdi"
                  :path="iconsStore.mdiCloseCircleOutline"
                ></svg-icon>
                <svg-icon
                  v-else-if="newTask.status === 'finished'"
                  class="taskDetailsHeaderStatusIcon finished"
                  type="mdi"
                  :path="iconsStore.successIcon"
                ></svg-icon>
                <svg-icon
                  v-else-if="newTask.status === 'inProgress'"
                  class="taskDetailsHeaderStatusIcon inProgress"
                  type="mdi"
                  :path="iconsStore.mdiDotsHorizontalCircleOutline"
                ></svg-icon>
                <svg-icon
                  v-else
                  class="taskDetailsHeaderStatusIcon available"
                  type="mdi"
                  :path="iconsStore.mdiRecordCircleOutline"
                ></svg-icon>
                <div>
                  <input
                    :placeholder="task.title"
                    v-model="newTask.titleInput"
                    maxlength="20"
                    :disabled="!isEditActive"
                  />
                  <span
                    @click="copyLink()"
                    id="taskDetailsID"
                    class="pointer"
                    title="Skopiuj link do zadania"
                    >{{ `#${task._id}` }}</span
                  >
                </div>
                <div id="taskDetailsButtonsContainer">
                  <div
                    :style="styles.deleteButton"
                    class="taskDetailsButton Delete pointer"
                  >
                    <div @click="buttonClicked('delete')" title="Usuń">
                      <svg-icon
                        type="mdi"
                        :path="iconsStore.mdiTrashCanOutline"
                      ></svg-icon>
                    </div>
                    <div @click="hideButtons('delete')" title="Anuluj">
                      <svg-icon
                        type="mdi"
                        :path="iconsStore.mdiClose"
                      ></svg-icon>
                    </div>
                  </div>
                  <div
                    :style="styles.editButton"
                    class="taskDetailsButton Edit pointer"
                  >
                    <div @click="buttonClicked('edit')" title="Wprowadź zmiany">
                      <svg-icon
                        type="mdi"
                        :path="iconsStore.mdiPencilOutline"
                      ></svg-icon>
                    </div>
                    <div @click="hideButtons('edit')" title="Anuluj zmiany">
                      <svg-icon
                        type="mdi"
                        :path="iconsStore.mdiClose"
                      ></svg-icon>
                    </div>
                  </div>
                </div>
              </div>
              <div id="taskDetailsDesc" class="formTextArea formItem">
                <label for="descInput">Opis</label>
                <div class="border">
                  <textarea
                    :placeholder="task.desc"
                    rows="5"
                    maxlength="300"
                    v-model="newTask.descInput"
                    :disabled="!isEditActive"
                  ></textarea>
                  <span class="letterCount">
                    {{
                      `${lettersCount.descLetters}/${lettersCount.maxDescLetters}`
                    }}
                  </span>
                </div>
              </div>
              <div id="taskDetailsFooter">
                <div id="taskDetailsTags">
                  <h1>Tagi:</h1>
                  <div id="taskDetailsTagsContainer">
                    <div
                      @click="toggleImportant()"
                      class="statusBadge taskStatus important"
                      :class="{
                        disabled: !newTask.important,
                        pointer: isEditActive,
                        editActived: isEditActive,
                      }"
                    >
                      <div>
                        <p>Ważne</p>
                      </div>
                    </div>
                    <div
                      @click="changeStatus('available', newTask.status)"
                      v-if="isEditActive || newTask.status === 'available'"
                      class="statusBadge taskStatus available"
                      :class="{
                        disabled: newTask.status !== 'available',
                        pointer: isEditActive,
                        editActived: isEditActive,
                      }"
                    >
                      <div>
                        <p>Dostępne</p>
                      </div>
                    </div>
                    <div
                      @click="changeStatus('inProgress', newTask.status)"
                      v-if="isEditActive || newTask.status === 'inProgress'"
                      class="statusBadge taskStatus inProgress"
                      :class="{
                        disabled: newTask.status !== 'inProgress',
                        pointer: isEditActive,
                        editActived: isEditActive,
                      }"
                    >
                      <div>
                        <p>W trakcie</p>
                      </div>
                    </div>
                    <div
                      @click="changeStatus('finished', newTask.status)"
                      v-if="isEditActive || newTask.status === 'finished'"
                      class="statusBadge taskStatus finished"
                      :class="{
                        disabled: newTask.status !== 'finished',
                        pointer: isEditActive,
                        editActived: isEditActive,
                      }"
                    >
                      <div>
                        <p>Ukończone</p>
                      </div>
                    </div>
                    <div
                      @click="changeStatus('cancelled', newTask.status)"
                      v-if="isEditActive || newTask.status === 'cancelled'"
                      class="statusBadge taskStatus cancelled"
                      :class="{
                        disabled: newTask.status !== 'cancelled',
                        pointer: isEditActive,
                        editActived: isEditActive,
                      }"
                    >
                      <div>
                        <p>Anulowane</p>
                      </div>
                    </div>
                  </div>
                </div>
                <div id="taskDetailsAuthor">
                  <AuthorCard :authorID="task.createdBy" :isFlipped="true" />
                </div>
              </div>
            </div>
          </section>
        </section>
      </div>
    </main>
  </body>
</template>

<script>
import API from "../api";
import svgIcon from "@jamescoyle/vue-icon";
import popupSound from "../assets/audio/popup.wav";

import { useAlertsStore } from "../stores/alertsStore";
import { usePreferenceStore } from "../stores/preferenceStore";
import { useIconsStore } from "../stores/iconsStore";
import { useGeneralStore } from "../stores/generalStore";

import HeaderLogo from "../components/HeaderLogo.vue";
import Notification from "../components/Assets/Notification.vue";
import UserCard from "../components/Assets/UserCard.vue";
import Navigation from "../components/Dashboard/MainNavigation.vue";
import AuthorCard from "../components/Assets/AuthorCard.vue";

export default {
  name: "TaskDetails",
  components: {
    svgIcon,
    HeaderLogo,
    Notification,
    UserCard,
    Navigation,
    AuthorCard,
  },
  setup() {
    const alertsStore = useAlertsStore();
    const iconsStore = useIconsStore();
    const generalStore = useGeneralStore();
    const preferenceStore = usePreferenceStore();

    return { alertsStore, iconsStore, generalStore, preferenceStore };
  },
  data() {
    return {
      newTask: [
        {
          titleInput: "Tytuł",
          descInput:
            "Szczegóły zadania nie zostały wczytane. Spróbuj zresetować stronę.",
          status: "available",
          important: false,
        },
      ],
      isEditActive: false,
      buttonClickedCount: {
        delete: 0,
        edit: 0,
      },
      styles: {
        editMsg: {
          top: "-50px",
        },
        deleteButton: {
          width: "30px",
        },
        editButton: {
          width: "30px",
        },
      },
      lettersCount: {
        descLetters: 0,
        maxDescLetters: 300,
      },
      task: [],
      tasks: [],
    };
  },
  methods: {
    async buttonClicked(button) {
      if (button === "delete") {
        this.styles.deleteButton.width = "65px";
        this.isEditActive = false;
      } else if (button === "edit") {
        this.isEditActive = true;
        this.styles.deleteButton.width = "30px";
      }

      if (button === "delete") {
        this.buttonClickedCount.delete++;
        this.buttonClickedCount.edit = 0;

        if (this.buttonClickedCount.delete === 2) {
          const response = await API.deleteTask(this.task);

          await this.alertsStore.showNotification(
            response.duration,
            response.status,
            response.message
          );

          if (this.preferenceStore.preferences[0].state) {
            const notificationSound = new Audio(popupSound);
            notificationSound.volume = 0.5;
            notificationSound.play();
          }

          if (response.status === "success")
            this.$router.push("/dashboard/zadania");

          this.buttonClickedCount.delete = 0;
        }
      }

      if (button === "edit") {
        this.buttonClickedCount.edit++;
        this.buttonClickedCount.delete = 0;

        if (this.buttonClickedCount.edit === 2) {
          const task = {
            _id: this.task._id,
            title: this.newTask.titleInput,
            desc: this.newTask.descInput,
            status: this.newTask.status,
            important: this.newTask.important,
          };

          const response = await API.updateTask(task);

          await this.alertsStore.showNotification(
            response.duration,
            response.status,
            response.message
          );

          if (this.preferenceStore.preferences[0].state) {
            const notificationSound = new Audio(popupSound);
            notificationSound.volume = 0.5;
            notificationSound.play();
          }

          if (response.status === "success")
            this.tasks = await API.getAllTasks();

          this.buttonClickedCount.edit = 0;

          this.isEditActive = false;
        }
      }
    },
    hideButtons(button) {
      if (button === "delete") {
        this.styles.deleteButton.width = "30px";
      } else if (button === "edit") {
        this.isEditActive = false;
      }

      this.buttonClickedCount.delete = 0;
      this.buttonClickedCount.edit = 0;
    },
    toggleImportant() {
      if (this.isEditActive) this.newTask.important = !this.newTask.important;
    },
    async changeStatus(changeTo, actualStatus) {
      if (!this.isEditActive) return;
      if (changeTo === actualStatus) return;

      this.newTask.status = changeTo;
    },
    async changeTask(destination) {
      this.$router.push(`/dashboard/zadania/${destination}`);

      this.isEditActive = false;
      this.buttonClickedCount.delete = 0;
      this.buttonClickedCount.edit = 0;
    },
    async copyLink() {
      const url = `${window.location.origin}/#/dashboard/zadania/${this.task._id}`;

      navigator.clipboard.writeText(url);

      await this.alertsStore.showNotification(4, "info", "Skopiowano link");

      if (this.preferenceStore.preferences[0].state) {
        const notificationSound = new Audio(popupSound);
        notificationSound.volume = 0.5;
        notificationSound.play();
      }
    },
  },
  watch: {
    isEditActive() {
      if (this.isEditActive) {
        this.styles.editMsg.top = "10px";
        this.styles.editButton.width = "65px";
      } else {
        this.styles.editMsg.top = "-50px";
        this.styles.editButton.width = "30px";
      }
    },
  },
  async beforeMount() {
    this.tasks = await API.getAllTasks();
  },
  async updated() {
    if (!this.isEditActive) {
      this.task = await this.tasks.find((task) => {
        return task._id === this.$route.params.id;
      });

      if (this.task === undefined)
        this.$router.push(`/${this.$route.params.id}`);

      this.newTask.titleInput = this.task.title;
      this.newTask.descInput = this.task.desc;
      this.newTask.status = this.task.status;
      this.newTask.important = this.task.important;
    }

    this.lettersCount.descLetters = this.newTask.descInput.length;
  },
};
</script>

<style lang="scss" scoped>
body {
  background-color: #20222a;

  #mainTasksSection {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 75%;

    #mainTasksContainer {
      width: 1200px;
      height: 100%;
      margin: auto;

      #tasksSection {
        display: grid;
        grid-template-areas:
          "tasksHeader taskDetails"
          "tasksList taskDetails";
        grid-template-columns: 350px auto;
        grid-template-rows: 140px auto;
        gap: 10px;
        width: 100%;
        height: 600px;
        border-radius: 5px;
        padding: 20px;
        background: linear-gradient(to bottom right, #20232a, #1a1c23);
        box-shadow: 0 4px 4px #181920;

        #tasksHeader {
          grid-column: 1 / 2;
          grid-row: 1 / 2;
          display: flex;
          flex-flow: column nowrap;
          justify-content: center;
          align-items: center;

          .tasksHeaderElement {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            width: 100%;
            height: 50px;
            margin: 10px;
            color: #d0d0d0;
            font-size: 20px;
            font-weight: bold;
            border: 1px solid #282c35;
            border-radius: 5px;
            box-shadow: 0 4px 4px #181920;
            transition: 0.2s ease-in-out;

            a {
              display: flex;
              flex-flow: row nowrap;
              align-items: center;
              width: 100%;
              height: 100%;
            }

            svg {
              --sx: 1.25 !important;
              --sy: 1.25 !important;
              margin: 10px;
            }

            &.active,
            &:hover {
              box-shadow: 0 4px 4px #181920;
              background-color: #323846;
            }
          }
        }

        #tasksList {
          grid-column: 1 / 2;
          grid-row: 2 / 3;
          padding: 5px;
          border: 1px solid #282c35;
          border-radius: 5px;
          box-shadow: 0 4px 4px #20232a;
          overflow: hidden;

          #tasksListHeader {
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            align-items: center;
            padding: 5px;
            color: #3c4454;
            transition: 0.2s ease-in-out;

            span {
              width: 50px;
              margin-right: 5px;
              user-select: none;
            }

            hr {
              width: 100%;
              height: 1px;
              border: none;
              background: linear-gradient(to right, #3c4454, #1d2026);
            }
          }

          #tasksListScroll {
            height: 100%;
            margin: 0;
            padding: 5px;
            overflow-y: scroll;

            &::-webkit-scrollbar {
              width: 10px;
            }

            &::-webkit-scrollbar-track {
              background: #282c35;
              border-radius: 5px;
            }

            &::-webkit-scrollbar-thumb {
              background: #105192;
              border-radius: 5px;
            }

            &::-webkit-scrollbar-thumb:hover {
              background: #0e4275;
            }

            .taskListElement {
              width: 100%;
              height: 50px;
              border-radius: 5px;
              margin: 5px 0;
              transition: 0.1s ease-in-out;

              div {
                display: flex;
                flex-flow: row nowrap;
                align-items: center;
                width: 100%;
                height: 100%;
              }

              .taskListElementStatus {
                width: 15%;
                --sx: 1.5 !important;
                --sy: 1.5 !important;

                &.available {
                  color: #d3d6dd;
                }
                &.inProgress {
                  color: #5886c3;
                }
                &.finished {
                  color: #59e567;
                }
                &.cancelled {
                  color: #c71a1a;
                }
              }

              .taskListElementDetails {
                display: flex;
                flex-flow: column nowrap;
                justify-content: center;
                align-items: flex-start;
                width: 75%;
                overflow: hidden;

                .taskListElementID {
                  margin-left: 5px;
                  color: #999999;
                  font-size: 12px;
                }

                .taskListElementTitle {
                  margin: 0;
                  color: #d0d0d0;
                  font-size: 20px;
                  white-space: nowrap;
                }
              }

              .taskListElementStar {
                width: 10%;
                height: 20px;
                color: #dcb364;
              }

              &.active,
              &:hover,
              .router-link-exact-active {
                background-color: #323846;
              }
            }
          }
        }
      }

      #taskDetailsSection {
        grid-column: 2 / 3;
        grid-row: 1 / 3;
        border-radius: 5px;
        border: 1px solid #282c35;
        box-shadow: 0 4px 4px #181920;

        #taskDetails {
          position: relative;
          display: grid;
          grid-template-columns: 100%;
          grid-template-rows: 120px auto 50px;
          height: 100%;
          padding: 10px 50px 50px;
          overflow: hidden;

          #taskDetailsEditMessage {
            position: absolute;
            right: 25%;
            padding: 10px 25px;
            color: #d3d6dd;
            border-radius: 5px;
            border: 1px solid #134c83;
            box-shadow: 0 4px 4px #181920;
            transition: 0.3s ease-in-out;
            user-select: none;
          }

          #taskDetailsHeader {
            grid-column: 1;
            grid-row: 1 / 2;

            display: grid;
            grid-template-columns: 50px auto 80px;
            grid-template-rows: 100%;
            column-gap: 1em;
            align-items: center;

            .taskDetailsHeaderStatusIcon {
              grid-column: 1 / 2;
              grid-row: 1;
              width: 48px;
              height: 48px;

              &.available {
                color: #d3d6dd;
              }
              &.inProgress {
                color: #5886c3;
              }
              &.finished {
                color: #59e567;
              }
              &.cancelled {
                color: #c71a1a;
              }
            }

            div {
              grid-column: 2 / 3;
              grid-row: 1;

              display: flex;
              flex-flow: column nowrap;
              align-items: flex-start;

              input {
                width: 100%;
                padding: 0;
                background: none;
                outline: none;
                border: none;
                color: #fff;
                font-size: 30px;
                white-space: nowrap;
                font-family: "Lato", sans-serif;
              }

              #taskDetailsID {
                margin-left: 0.5em;
                font-size: 18px;
                color: #43444a;
                transition: 0.1s ease-in-out;

                &:hover {
                  color: #3c3d42;
                }
              }
            }

            #taskDetailsButtonsContainer {
              grid-column: 3 / 4;
              grid-row: 1;
              display: flex;
              flex-flow: column nowrap;
              justify-content: center;
              align-items: flex-end;
              margin: 0 20px;

              .taskDetailsButton {
                position: relative;
                display: flex;
                justify-content: center;
                align-items: flex-start;
                height: 30px;
                margin: 5px;
                padding: 0;
                border: 1px solid;
                border-radius: 5px;
                box-shadow: 0 4px 4px #20232a;
                transition: 0.2s ease-in-out;
                overflow: hidden;
                user-select: none;

                div {
                  max-width: 28px;
                  height: 100%;
                  position: absolute;
                  align-items: center;
                  padding: 3px;
                  transition: 0.2s ease-in-out;

                  &:nth-of-type(2) {
                    position: absolute;
                    left: 35px;
                  }

                  &.Edit {
                    &:hover {
                      color: #0155a3;
                      border-color: #0155a3;
                    }
                  }
                }

                &.Edit {
                  color: #0270d7;
                  border-color: #0270d7;

                  div:hover {
                    color: #0155a3;
                    border-color: #0155a3;
                  }
                }

                &.Delete {
                  color: #b23d3d;
                  border-color: #b23d3d;

                  div:hover {
                    color: #862828;
                    border-color: #862828;
                  }
                }
              }
            }
          }

          #taskDetailsDesc {
            grid-column: 1;
            grid-row: 2 / 3;
          }

          #taskDetailsFooter {
            grid-column: 1;
            grid-row: 3 / 4;
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-between;
            align-items: center;

            #taskDetailsTags {
              display: flex;
              flex-flow: row nowrap;
              justify-content: flex-start;
              align-items: center;

              h1 {
                color: #999999;
              }

              #taskDetailsTagsContainer {
                display: flex;
                flex-flow: row wrap;
                justify-content: flex-start;
                align-items: center;

                .taskStatus {
                  margin: 5px;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
