<template>
  <div id="authorCard" :class="isFlipped ? 'left' : 'right'" title="Autor">
    <div id="authorImage">
      <img
        class="staticImg"
        src="../../assets/userAvatar.png"
        alt="User Avatar"
      />
    </div>
    <div id="authorDetails">
      <div id="authorInfo">
        <span id="authorNickname">{{ author.nick }}</span>
        <span id="authorRole">{{ author.role }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import API from "../../api";

export default {
  name: "AuthorCard",
  props: {
    authorID: {
      type: String,
    },
    authorObject: {
      nick: String,
      role: String,
    },
    isFlipped: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      author: [
        {
          nick: "Nieznany",
          role: "Nieznany",
        },
      ],
    };
  },
  beforeMount() {
    if (this.authorObject != undefined) {
      this.author = this.authorObject;
      return;
    }
  },
  async updated() {
    const response = await API.getUserNV({ user: this.authorID });

    if (response.content !== null) {
      this.author.nick = response.content.nick;
      this.author.role = response.content.roles[0].toString();

      return;
    }
    this.author.nick = "Nieznany";
    this.author.role = "Nieznany";
  },
};
</script>

<style lang="scss" scoped>
#authorCard {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: fit-content;
  height: 32px;
  background-color: #272727;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;

  &.left {
    flex-flow: row-reverse nowrap;
    border-radius: 10px 25px 25px 10px;

    #authorInfo {
      align-items: flex-end;
    }
  }
  &.right {
    flex-flow: row nowrap;
    border-radius: 25px 10px 10px 25px;

    #authorInfo {
      align-items: flex-start;
    }
  }

  #authorImage {
    height: 100%;

    img {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      box-shadow: 0 0 5px #9f9f9f;
    }
  }

  #authorDetails {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    padding: 0 15px;

    #authorInfo {
      display: flex;
      flex-flow: column nowrap;
      justify-content: center;
      white-space: nowrap;

      #authorNickname {
        font-size: 16px;
      }

      #authorRole {
        color: #999;
        font-size: 10px;
        text-transform: uppercase;
      }
    }
  }
}
</style>